.container {
    position: relative;
    width: 480px;
    border: 2px solid #5f155f;
    text-align: center;
    margin: 40px auto 10px;
    padding: 20px;
    border-radius: 20px;
    font-family: "Poppins", sans-serif;
}

.lol-png {
    position: absolute;
    width: 50px;
    height: 50px;
    top: -20px;
    right: -20px;
}

.header {
    display: flex;
    justify-content: space-between;
    margin: 0 4px 12px;
}

.header h2 {
    text-decoration: 2px solid #5f155f underline;
}

img.logo {
    margin: 0;
    width: 80px;
    height: 80px;
}

hr {
    width: 100%;
    background-color: #5f155f;
    height: 2px;
    border-radius: 10px;
}

.btn {
    display: block;
    border: none;
    border-radius: 10px;
    background: #5f155f;
    color: #FFF;
    width: 80%;
    padding: 10px;
    margin: 0 auto 8px;
    font-size: 16px;
    cursor: pointer;
}

.btn:active {
    background-color: #FFF;
    color: #000;
    border: 2px solid #5f155f;
}

input {
    margin: 4px 6px;
    border: none;
    border: 2px solid #d3a4d3;
    border-radius: 4px;
    height: 18px;
}

.image-body {
    position: relative;
    max-width: 350px;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.top-text, .bottom-text {
    position: absolute;
    font-size: 24px;
    font-family: sans-serif;
    text-transform: uppercase;
    margin: 0 35px;
    width: 100%;
    color: #FFF;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.top-text {
    top: 4px;
}

.bottom-text {
    bottom: 4px;
}

img {
    display: block;
    max-width: 350px;
    margin: auto;
}

@media only screen and (max-width: 720px) {
    .container {
        margin: 20px 8px;
    }
}